.con-img {
    width: 100%; /* m bo nh chim ton b chiu rng ca phn t cha */
    height: 100%; /* m bo nh chim ton b chiu cao ca phn t cha */
    display: flex;
    justify-content: center; /* Cn gia nh ngang */
    align-items: center; /* Cn gia nh dc */
    
  }
  
  .con-img img {
    width: 100%; /* m bo nh c chiu rng 100% */
    height: 100%; /* m bo nh c chiu cao 100% */
    object-fit: fit; /* Gi t l ca nh v ct bt phn tha nu cn thit */
    border-radius: 90px;
  }
  .title {
    height: auto;
    flex-shrink: 0;
    
    text-shadow: 0px 3px 9px rgba(255, 168, 0, 0.50);
    font-family: Adamina;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: linear-gradient(180deg, #F6E336 32.76%, #E48413 87.93%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 10px;
}

.content {
    margin-left: 10px;
    margin-bottom: 10px;
    height: auto;
    flex-shrink: 0;
    align-self: stretch;
    color: #FFF;
    font-family: Inter;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-align: left;
}
.constellation{
    box-sizing: border-box;
    border-radius: 90px;
    padding: 0 30px 0 30px ;
    margin: 30px 0 30px 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 46.875rem;
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
}
.constellation::-webkit-scrollbar {
    display: none; /* n scrollbar trn WebKit */
}
